@import "components/config";

*, *:before, *:after {
  box-sizing: border-box; }

body, html {
  height: 100%; }

body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: $default-font;
  color: $text-color;
  line-height: $l-height;
  font-size: $f-size;
  overflow-x: hidden; }

ul, ul li {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%; }
.content {
  flex: 1 0 auto; }
.footer {
  flex: 0 0 auto; }

.container {
  max-width: $container-size;
  width: 100%;
  margin: 0 auto;
  padding: 0 $container-padding; }

